@import "../core/variables";
@import "../core/mixins";

.charDetail {
  margin: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  &__container {
    border: 4px solid $teal;
    background-color: $lightgrey;
    padding: 20px;
    border-radius: 10%;
    max-width: 90vw;
  }
  &__title {
    font-family: $fontMain;
    text-align: center;
    font-size: 34px;
    margin: 0 0 20px 0;
  }
  &__card {
    @include tablet {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 50px;
    }
  }
  &__props {
    list-style-type: none;
    font-family: $fontText;
    line-height: 2;
    font-size: 16px;
    @include tablet {
      font-size: 18px;
    }
    @include desktop {
      font-size: 20px;
    }
  }
  &__link {
    font-family: $fontMain;
    color: $orange;
    font-size: 20px;
    line-height: 2;
    margin: 0 120px;
  }
  &__image {
      width: 100%;
      max-width: 300px;
    @include tablet {
      margin: 0 auto 0 20px;
    }
  }
}
